@import '../../../assets/scss/variables';

.chat-details-container {
  max-width: 1200px;
  margin: 2rem auto;
  padding: 2rem;
  background: $dark;
  border-radius: 16px;
  box-shadow: 0 12px 35px rgba(0, 0, 0, 0.25);
  color: $lightest;

  .chat-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid rgba($lightest, 0.1);

    h1 {
      font-size: 2rem;
      color: $lightest;
      margin: 0;
    }

    .chat-meta {
      display: flex;
      align-items: center;
      gap: 1rem;

      span {
        color: rgba($lightest, 0.7);
        font-size: 0.9rem;
      }

      .delete-button {
        padding: 0.5rem 1rem;
        background: rgba($red-main, 0.2);
        color: $lightest;
        border: none;
        border-radius: 6px;
        cursor: pointer;
        transition: all 0.2s ease;

        &:hover {
          background: rgba($red-main, 0.4);
        }
      }
    }
  }

  .chat-content {
    display: grid;
    grid-template-columns: 1fr;
    gap: 2rem;

    .chat-description {
      padding: 1.5rem;
      background: rgba($darkest, 0.5);
      border-radius: 12px;
      line-height: 1.6;
    }

    .messages-container {
      max-height: 60vh;
      overflow-y: auto;
      padding-right: 1rem;
      margin-bottom: 2rem;

      &::-webkit-scrollbar {
        width: 8px;
      }

      &::-webkit-scrollbar-track {
        background: rgba($darkest, 0.5);
        border-radius: 10px;
      }

      &::-webkit-scrollbar-thumb {
        background: rgba($lightest, 0.2);
        border-radius: 10px;
      }

      &::-webkit-scrollbar-thumb:hover {
        background: rgba($lightest, 0.3);
      }

      .message {
        background: rgba($darkest, 0.7);
        border-radius: 12px;
        padding: 1.2rem;
        margin-bottom: 1rem;
        backdrop-filter: blur(8px);
        border: 1px solid rgba($lightest, 0.08);
        transition: all 0.3s ease;

        &.own-message {
          background: rgba($blue-main, 0.15);
          border-color: rgba($blue-main, 0.3);
        }

        &:hover {
          transform: translateX(5px);
        }

        .message-header {
          display: flex;
          align-items: center;
          gap: 1rem;
          margin-bottom: 0.8rem;

          .message-avatar {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            object-fit: cover;
            border: 2px solid rgba($lightest, 0.2);
          }

          .message-info {
            display: flex;
            flex-direction: column;

            .message-user {
              font-weight: 600;
              color: $lightest;
            }

            .message-time {
              font-size: 0.8rem;
              color: rgba($lightest, 0.6);
            }
          }
        }

        .message-text {
          color: rgba($lightest, 0.9);
          line-height: 1.6;
          padding-left: 3.5rem;
        }
      }
    }
  }

  .chat-creator {
    margin: 2rem 0;
    padding: 1.5rem;
    background: rgba($darkest, 0.5);
    border-radius: 12px;

    h3 {
      margin-top: 0;
      margin-bottom: 1rem;
      color: $lightest;
    }

    .creator-link {
      display: flex;
      align-items: center;
      gap: 1rem;
      text-decoration: none;
      color: $lightest;

      .creator-avatar {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        object-fit: cover;
        border: 2px solid rgba($lightest, 0.2);
      }

      &:hover {
        color: $blue-main;
      }
    }
  }

  .related-chats {
    margin: 2rem 0;

    h3 {
      margin-bottom: 1.5rem;
      color: $lightest;
    }

    .chats-grid {
      display: grid;
      grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
      gap: 1.5rem;

      .chat-card-small {
        background: rgba($darkest, 0.7);
        border-radius: 12px;
        overflow: hidden;
        transition: transform 0.3s ease;

        &:hover {
          transform: translateY(-5px);
        }

        a {
          text-decoration: none;
          color: $lightest;
          display: block;

          img {
            width: 100%;
            height: 120px;
            object-fit: cover;
          }

          h4 {
            padding: 1rem;
            margin: 0;
            font-size: 1rem;
          }
        }
      }

      p {
        color: rgba($lightest, 0.7);
        font-style: italic;
      }
    }
  }

  .message-input {
    display: flex;
    gap: 1rem;
    margin-top: 2rem;

    input {
      flex: 1;
      padding: 1rem;
      background: rgba($darkest, 0.7);
      border: 1px solid rgba($lightest, 0.1);
      border-radius: 12px;
      color: $lightest;
      font-size: 1rem;
      transition: all 0.3s ease;
      backdrop-filter: blur(5px);

      &:focus {
        outline: none;
        border-color: $blue-main;
        box-shadow: 0 0 0 2px rgba($blue-main, 0.3);
      }

      &::placeholder {
        color: rgba($lightest, 0.5);
      }

      &:disabled {
        opacity: 0.5;
        cursor: not-allowed;
      }
    }

    button {
      padding: 0 2rem;
      background: linear-gradient(135deg, $blue-main 0%, darken($blue-main, 10%) 100%);
      color: $lightest;
      border: none;
      border-radius: 12px;
      font-weight: 600;
      cursor: pointer;
      transition: all 0.3s ease;
      box-shadow: 0 5px 15px rgba($blue-main, 0.2);

      &:hover:not(:disabled) {
        transform: translateY(-2px);
        box-shadow: 0 8px 20px rgba($blue-main, 0.3);
      }

      &:active:not(:disabled) {
        transform: translateY(0);
      }

      &:disabled {
        background: rgba($lightest, 0.1);
        cursor: not-allowed;
        opacity: 0.7;
      }
    }
  }

  .loading, .error {
    text-align: center;
    padding: 2rem;
    font-size: 1.2rem;
  }

  .loading {
    color: $blue-main;
  }

  .error {
    color: $red-main;
  }
}

@media (max-width: 768px) {
  .chat-details-container {
    padding: 1.5rem;

    .chat-header {
      flex-direction: column;
      align-items: flex-start;
      gap: 1rem;

      .chat-meta {
        width: 100%;
        justify-content: space-between;
      }
    }

    .related-chats {
      .chats-grid {
        grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
      }
    }

    .message-input {
      flex-direction: column;

      button {
        padding: 1rem;
      }
    }
  }
}

@media (max-width: 576px) {
  .chat-details-container {
    padding: 1rem;
    border-radius: 0;

    .message {
      .message-text {
        padding-left: 0;
      }
    }
  }
}

.delete-message-button {
    background: none;
    border: none;
    color: $red-main;
    font-size: 14px;
    cursor: pointer;
    margin-left: 8px;
  }
  